<template>
  <div id="chat">
    <div id="header">
      <div id="chatmes">
        <p>{{$store.state.nowchat}}</p>
      </div>
    </div>
    <div id="chatcontent">
        <ul v-for="item in $store.state.chatmes">
          <p class="left_item" v-if="item.from==$store.state.nowchat">
            {{item.mes}}
          </p>
          <p class="right_item" v-if="item.from!=$store.state.nowchat">
            {{item.mes}}
          </p>
        </ul>
    </div>
    <div id="chatinput">
        <div id="emoji" v-if="faceShow">
          <li v-for="(item,index) in face" @click="choose_face(item)">
            {{item}}
          </li>
        </div>
        <img src="../../assets/emoji.png" @click="faceContent()">
        <input type="textarea" v-model="mymes">
        <img src="../../assets/send.png" @click="post()">
      </div>
  </div>
</template>

<script>
import axios from "axios";
const appData = require("../../assets/emojis.json");
export default {
  name: "chat",
  data() {
    return {
      menu:false,
      myid: "",
      mymes: "",
      face: [],
      faceShow: false
    };
  },
  mounted() {
    let data = { username: this.$store.state.username };
    this.$socket.emit("getid", data);
  },
  sockets: {
    chatmes: function(data) {
      if (data.from == this.$store.state.nowchat) {
        this.$store.commit("pushchatmes", {
          from: data.from,
          to: data.to,
          mes: data.mes
        });
      }
      this.$store.commit("pushchatlist", data.from);
    }
  },
  methods: {
    showlist(){
      this.menu=!this.menu;
    },
    faceContent() {
      this.faceShow = !this.faceShow;
      if (this.faceShow == true) {
        for (let i in appData) {
          this.face.push(appData[i].char);
        }
      } else {
        this.face = [];
      }
    },
    choose_face(data) {
      this.mymes += data;
    },
    post() {
      if (this.mymes == "") return;
      let data = {
        type:'user',
        from: this.$store.state.username,
        to: this.$store.state.nowchat,
        mes: this.mymes,
        time: new Date()
      };
      this.$socket.emit("mes", data);
      this.$store.commit("pushchatmes", {
        from: data.from,
        to: data.to,
        mes: data.mes
      });
      this.mymes = "";
      this.$store.commit("pushchatlist", data.to);
    }
  }
};
</script>

<style lang="less" scoped>
li {
  list-style: none;
}
#chat {
  height: 100%;
  display: flex;
  flex-direction: column;
}
 #menu{
    z-index: 1000;
    width: 30px;
    height: 30px;
    position: absolute;
    top:1%;
    left: 1%;
  }
#chatlist{
  position: absolute;
  height: 50%;
  width: 100%
}
#header {
  height: 12%;
  border-bottom: 1px rgb(194, 194, 194) solid;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  #chatmes {
    margin: 30px;
    font-size: 25px;
  }

}
#chatcontent {
  //height:88%;
  width: 90%;
  margin-left: 5%;
  height: 75%;
  overflow: scroll;
  .right_item {
    text-align: right;
  }
}
#chatinput {
  margin-left: 5%;
  margin-top: 40px;
  margin-bottom: 40px;
  width: 90%;
  display: flex;
  align-items: center;
  flex: 1;
  #emoji {
    width: 300px;
    display: flex;
    flex-wrap: wrap;
    background: pink;
  }
  input {
    height: 40px;
    border: 1px rgb(185, 185, 185) solid;
    font-size: 20px;
    display: block;
    flex: 1;
  }
  img {
    height: 40px;
    width: 40px;
    border: 1px rgb(185, 185, 185) solid;
  }
}
</style>



