<template>
  <div id="chatlist">
    <div id="headbar">
      <p>会话</p>
    </div>
    <div id="list">
      <input type="text" @keyup.enter="find_user(username)" v-model="username">
      <li v-for="(item,index) in $store.state.chatlist" class="item" @click="choose_chat(index)">
          <img src="../../assets/close.png" @click="rmv_chat()">
          <p>{{item}}</p>
      </li>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "chatlist",
  data() {
    return {
      username: ""
    };
  },
  mounted() {
    let $this = this;
    axios
      .get(
        `http://111.230.63.143:3005/getchatlist?name=${
          this.$store.state.username
        }`
      )
      .then(res => {
        $this.$store.commit("getchatlist", res.data);
      });
  },
  methods: {
    choose_chat: function(index) {
      let $this = this;
      this.$store.commit("setnowchat", this.$store.state.chatlist[index]);
      this.$store.commit("getchatmes");
    },
    find_user: function(data) {
      axios.get(`http://111.230.63.143:3005/ifuser?name=${data}`).then(res => {
        res.data == 0
          ? alert("内部暂无此人!")
          : this.$store.commit("setnowchat", data);
        this.$store.commit("getchatmes");
        this.username = "";
      });
    },
    rmv_chat: function() {
      alert(1);
    }
  }
};
</script>

<style lang="less" scoped>
#chatlist {
  background-color: rgb(77, 77, 77);
}
#headbar {
  height: 12%;
  border-bottom: 1px rgb(194, 194, 194) solid;
  display: flex;
  align-items: center;
  justify-content: center;
  p {
    font-size: 30px;
    color: white;
    font-weight: bold;
  }
}
#list {
  height: 88%;
  text-align: center;
  padding-top: 20px;
  input {
    width: 70%;
    height: 30px;
    border: 0px;
    outline: none;
    border-radius: 12px;
    font-size: 20px;
  }
  .item {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 70%;
    justify-content: flex-start;
    margin-top: 20px;
    margin-left: 15%;
    cursor: pointer;
    img {
      height: 40px;
      margin-right: 20px;
    }
    p {
      color: white;
    }
  }
  .item:hover {
    background: rgb(170, 170, 170);
  }
  .item:active {
    background: rgb(255, 255, 255);
  }
}

</style>



